@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
--sidebar-flex:1.5;
--condiv-flex:6;
--fontsize-p:1.2rem;
--fontsize-nav:1.6rem;
--typing-font:3rem;
}
*
{
margin:0;
padding: 0;
}
body
{
font-family: 'Raleway';
color: #05386b;
}
.App
{
display: -webkit-flex;
display: flex;
width: 100vw;
min-height: 100vh;
}

nav
{
padding:60px 30px 0px 30px ;
-webkit-flex:1.5;
        flex:1.5;
-webkit-flex:var(--sidebar-flex);
        flex:var(--sidebar-flex);
background: #379683;

}
.condiv
{
padding: 100px ;
background-color: #5cdb95;
-webkit-flex:6;
        flex:6;
-webkit-flex:var(--condiv-flex);
        flex:var(--condiv-flex);
}


nav ul
{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: #05386b;
}
nav ul li
{
  margin:40px;
}

.active
{
font-weight: bolder;
}

.subtopic
{
margin:10px 10px 30px 10px !important;
}
p
{
font-size: 1.2rem;
font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p
{
margin: 10px;
}
.widecard
{
border:0px solid #9a9b9c;
display: -webkit-flex;
display: flex;
margin:30px 0px 30px 0px;
}
.widecard:hover
{
color: #1b1d20;
background-color: #379683;
box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.footer{
  margin-top: 1rem;
  padding: 1rem;
  background-color: #8ee4af;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;

}
.typingeffect{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  color: #05386b;
}
.row{
  margin-bottom: 10px;
}
.a{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
